import React from 'react';
// import bg from './p1_bg.jpg';
import bg from './7b8d0df5988b9c33f5ce5135391f0aa6.jpg';
import itembg from '../../style/img/c205e9d4d2e847bd9920cb7805e8e15a.png';

export default class index extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isVisible: false
        }
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState(() => ({
                isVisible: true
            }));
        }, 1000)
    }

    render() {

        return (
            <div className="full-w full-h relative"
                 style={{backgroundImage: `url(${bg})`, backgroundSize: '100% 100%'}}>
                {this.state.isVisible &&
                <div className="full-w full-h relative" style={{backgroundColor: `rgba(0,0,0,.6)`}}>
                    <div
                        className="absolute top-0 left-0 right-0 bottom-0 m-auto flex flex-column justify-center items-center animated bounceInDown"
                        style={{
                            width: 200,
                            height: 300,
                        }}
                    >
                        <div className=''
                             style={{
                                 width: 200,
                                 height: 200,
                                 borderRadius: '6px',
                                 color: 'white',
                                 backgroundImage: `url(${itembg})`,
                                 backgroundSize: '100% 100%'
                             }}
                        />
                        <div className="flex items-center justify-center"
                             style={{width:60,height:60,borderRadius:'50%',fontSize:40,border:'1px solid white',color:'white'}}

                        >
                            Ã—
                        </div>
                    </div>
                </div>
                }
                <style>{
                    `

                     `
                }</style>
            </div>
        )
    }
}
