import React from 'react';
import {isMobile} from "../utils";
import P1 from './p1';
import mp from '../style/img/music_pointer.png'
import md from '../style/img/music_disc.png'
export default class index extends React.Component {
    constructor(props){
        super(props);
        this.state={
            playC:'play'
        }
    }
    music = ()=>{
        if(this.audio){
            if(this.audio.paused){
                this.audio.play();
                    this.setState(()=>({
                        playC:'play'
                    }));
            }else{
                this.audio.pause();
                this.setState(()=>({
                    playC:''
                }));
            }
        }
    };
    componentDidMount(){
        this.audio&&this.audio.play()
    }
    render(){
        const settings={
            style:Styles.wrap
            };
        const {playC}=this.state;
        return (
            <div {...settings} className="relative">
                <div className="music" onClick={this.music} onTouchStart={this.music}>
                    <img src={mp}/>
                    <img className={playC} id="music" src={md}/>
                </div>
                <P1/>
                <audio ref={(com)=>this.audio = com} controls className="foo" preload="auto" src="http://47.104.203.64:9010/hp.mp3"
                       type="audio/mpeg">
                    ...
                </audio>
                <style>{
                        `
                            .music {
                                position: absolute;
                                top: 3%;
                                right: 4%;
                                z-index: 5;
                                width: 50px;
                                height: 50px;
                                border: 4px solid #ef1639;
                                -webkit-border-radius: 50%;
                                border-radius: 50%;
                                background: #fff
                            }

                            .music > img:first-child {
                                position: absolute;
                                top: 24%;
                                right: 2.5%;
                                width: 28.421%;
                                z-index: 1
                            }

                            .music > img:last-child {
                                position: absolute;
                                top: 0;
                                right: 0;
                                bottom: 0;
                                left: 0;
                                margin: auto;
                                width: 79%;
                                z-index: 0
                            }

                            .music > img.play {
                                -webkit-animation: music_disc 4s linear infinite;
                                animation: music_disc 4s linear infinite
                            }

                            @-webkit-keyframes music_disc {
                                0% {
                                    -webkit-transform: rotate(0deg);
                                    transform: rotate(0deg)
                                }
                                100% {
                                    -webkit-transform: rotate(360deg);
                                    transform: rotate(360deg)
                                }
                            }

                            @keyframes music_disc {
                                0% {
                                    -webkit-transform: rotate(0deg);
                                    transform: rotate(0deg)
                                }
                                100% {
                                    -webkit-transform: rotate(360deg);
                                    transform: rotate(360deg)
                                }
                            }


                        `
                }</style>
            </div>
        )
    }
}
const Styles = {
    wrap:{
        width:isMobile() ? '100vw' : '375px',
        height:isMobile() ? '100vh':'812px',
        backgroundColor:'lightblue'
    }
};